@use './variables' as var;

.gameboard{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;

  .score{
    display: flex;
    gap: 2rem;
    font-size: var(--small-font-size);

    .score__field{
      display: flex;
      align-items: center;
      justify-content: center;
      background: hsl(var.$hue, var.$sat, 90);
      width: 8rem;
      height: 3rem;
      border-radius: .5rem;
      box-shadow: 0px 0px 5px 2px #333;

      &--highest{
	background: hsl(200, 30, 70);
      }
    }
  }

  .cards-dashboard{
    display: grid;
    width: min(90%, 40rem);
    grid-template-columns: repeat(3, 1fr);
    border-radius: .5rem;
    padding: 1rem;
    grid-gap: .75rem;
  }

  @include var.respond-to('medium'){
    .cards-dashboard{
      height: 100%;
      grid-gap: 2rem;
    }
  }
}
